"use client";

import { useState } from "react";
import Link from "next/link";
import { Card, CardHeader, CardTitle, CardDescription, CardContent, CardFooter } from "@/components/ui/card";
import { Table, TableHeader, TableRow, TableHead, TableBody, TableCell } from "@/components/ui/table";
import { Button } from "@/components/ui/button";
import { pharmacyPrescriptions } from "@/lib/data";
import { ShoppingCart, PlusCircle, MinusCircle, Trash2, CreditCard } from "lucide-react";
import { Badge } from "@/components/ui/badge";

type CartItem = {
  id: number;
  name: string;
  dosage: string;
  price: number;
  quantity: number;
};

export default function PharmacyPage() {
  const [cart, setCart] = useState<CartItem[]>([]);

  const addToCart = (medication: typeof pharmacyPrescriptions[0]) => {
    setCart((prevCart) => {
      const existingItem = prevCart.find((item) => item.id === medication.id);
      if (existingItem) {
        return prevCart.map((item) =>
          item.id === medication.id
            ? { ...item, quantity: item.quantity + 1 }
            : item
        );
      }
      return [...prevCart, { ...medication, quantity: 1 }];
    });
  };

  const updateQuantity = (id: number, amount: number) => {
    setCart((prevCart) => {
      return prevCart
        .map((item) => {
          if (item.id === id) {
            const newQuantity = item.quantity + amount;
            return newQuantity > 0 ? { ...item, quantity: newQuantity } : null;
          }
          return item;
        })
        .filter((item): item is CartItem => item !== null);
    });
  };
  
  const total = cart.reduce((sum, item) => sum + item.price * item.quantity, 0);

  return (
    <div className="space-y-8">
      <Card>
        <CardHeader>
          <CardTitle>Your Prescriptions</CardTitle>
          <CardDescription>
            Order your prescribed medications for convenient home delivery.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Medication</TableHead>
                <TableHead>Dosage</TableHead>
                <TableHead>Price</TableHead>
                <TableHead>Prescribed by</TableHead>
                <TableHead className="text-right">Action</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {pharmacyPrescriptions.map((med) => (
                <TableRow key={med.id}>
                  <TableCell className="font-medium">{med.name}</TableCell>
                  <TableCell>{med.dosage}</TableCell>
                  <TableCell>${med.price.toFixed(2)}</TableCell>
                  <TableCell>{med.doctor}</TableCell>
                  <TableCell className="text-right">
                    <Button size="sm" onClick={() => addToCart(med)}>
                      <ShoppingCart className="mr-2 h-4 w-4" />
                      Add to Cart
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </CardContent>
      </Card>

      {cart.length > 0 && (
        <Card>
            <CardHeader>
                <CardTitle>Shopping Cart</CardTitle>
            </CardHeader>
            <CardContent>
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Item</TableHead>
                            <TableHead>Price</TableHead>
                            <TableHead className="text-center">Quantity</TableHead>
                            <TableHead className="text-right">Total</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {cart.map(item => (
                            <TableRow key={item.id}>
                                <TableCell className="font-medium">{item.name}</TableCell>
                                <TableCell>${item.price.toFixed(2)}</TableCell>
                                <TableCell className="text-center">
                                    <div className="flex items-center justify-center gap-2">
                                        <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => updateQuantity(item.id, -1)}><MinusCircle className="h-4 w-4" /></Button>
                                        <span>{item.quantity}</span>
                                        <Button variant="ghost" size="icon" className="h-6 w-6" onClick={() => updateQuantity(item.id, 1)}><PlusCircle className="h-4 w-4" /></Button>
                                    </div>
                                </TableCell>
                                <TableCell className="text-right font-medium">${(item.price * item.quantity).toFixed(2)}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </CardContent>
            <CardFooter className="flex flex-col items-end gap-4">
                <div className="text-xl font-bold">Total: ${total.toFixed(2)}</div>
                <Button asChild>
                    <Link href="/dashboard/pharmacy/payment">
                        Proceed to Checkout <CreditCard className="ml-2 h-4 w-4" />
                    </Link>
                </Button>
            </CardFooter>
        </Card>
      )}

      <div className="text-center">
        <Button variant="secondary" asChild>
            <Link href="/dashboard/pharmacy/tracking">
                View Order History
            </Link>
        </Button>
      </div>
    </div>
  );
}
